<template>
  <div>
    <div class="headerFixed backgroundNaviBar">
      <van-nav-bar
        :fixed="true"
        title="应急运维"
        right-text="新增"
        @click-left="()=>{
          $router.go(-1)
         }"
         @click-right="addControl"
        left-arrow
        class="backgroundNaviBar"
        z-index="999"
        :border="false"
      >
        <!-- <van-icon name="search" slot="right" /> -->
      </van-nav-bar>
      <div class="boxBottom">
        <div class="condition">
          <span>筛选条件</span>
        </div>
        <!-- <div class="timeChoice">时间按钮</div> -->
        <div class="stationStyle" @click="()=>{
          showStation = true;
          }">
          <div style="flex:1">{{station===""?"所有站点":station}}</div>
          <!-- <div style="flex:1" class="imageSize">
            <span>废液转运</span>
            <img src="../../../static/img/bottomControl.png" alt="">
          </div> -->
        </div>
        <div
          class="timeSelect"
          @click="()=>{
            showTimeRange = true;
          }"
        >{{startTime}} ~ {{endTime}}</div>
      </div>
    </div>
    <div class="body">
      <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
          <div v-for="(item,index) in list" :key="index">
            <div class="bodyContent border-radius">
              <div class="bodyTime">
                <span>{{item.gmtCreate}}</span>
              </div>
              <div class="boxContent">
                <div>
                  <div class="heightCont">{{item.person}}</div>
                  <div
                    class="heightCont"
                    :style="item.solution===null?color:''"
                  >{{item.solution===null?"请尽快解决":item.solution.slice(0,5)+"..."}}</div>
                </div>
                <div v-show="item.status===1" class="resultCont" @click="toDetails(item)">
                  <div class="resultstyle">已解决</div>
                </div>
                <div v-show="item.status===0" class="resultCont" @click="toNoDetails(item)">
                  <div class="noresultstyle">未解决</div>
                </div>
              </div>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
    <van-popup v-model="showStation" position="bottom">
      <van-picker
        :columns="columns"
        show-toolbar
        :close-on-click-overlay="false"
        title="选择站点"
        :default-index="0"
        @cancel="()=>{
          showStation = false
        }"
        @confirm="onChange"
      />
    </van-popup>
    <van-calendar
      v-model="showTimeRange"
      type="range"
      @confirm="onConfirm"
      :show-confirm="false"
      :min-date="minDate"
      :max-date="maxDate"
      :default-date="defaultTime"
      color="#328efb"
    />
  </div>
</template>
<script>
import Vue from "vue";
import { List, PullRefresh } from "vant";
// import Vue from "vue";
import { Toast, Picker, Popup } from "vant";
import { Calendar } from "vant";

Vue.use(Calendar);
Vue.use(Toast)
  .use(Picker)
  .use(Popup);
Vue.use(List).use(PullRefresh);
import AppBace from "../../../static/ajax";
export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      token: "",
      color: "color:grey;font-size:12px;",
      total: 0,
      pageSize: 10,
      showStation: false,
      station: "",
      columns: this.$store.state.menu,
      showTimeRange: false,
      minDate: new Date(2019, 5, 1),
      maxDate: new Date(),
      defaultTime: new Date(),
      startTime: AppBace.filterTime(AppBace.DateTime(1)),
      endTime: AppBace.filterTime1(AppBace.DateTime(0))
    };
  },
  created() {
    this.token = sessionStorage.getItem("token");
    if (this.columns.indexOf("所有站点") === -1) {
      this.columns.push("所有站点");
    }
    this.initDataRequest();
  },
  methods: {
    onConfirm(val) {
      this.startTime = AppBace.filterTime(val[0]);
      this.endTime = AppBace.filterTime1(val[1]);
      
      this.initDataRequest();
      this.showTimeRange = false;
    },
    onChange(val) {
      if (val === "全部站点") {
        this.station = "";
      } else {
        this.station = val;
      }
      this.initDataRequest();
      this.showStation = false;
    },
    addControl() {
      this.$router.push("/OperationAndMaintenance");
    },
    toDetails(res) {
      this.$store.commit("controlFunc", res);
      this.$router.push({
        path: "MaintenanceDetails",
        query: {
          name: 1
        }
      });
    },
    toNoDetails(res) {
      this.$store.commit("controlFunc", res);
      this.$router.push({
        path: "MaintenanceDetails",
        query: {
          name: 0
        }
      });
    },
    async initDataRequest() {
      var url = "/job/operate-record/page";
      // var endTime = "";
      var endTime = this.endTime;
      var pageSize = this.pageSize;
      var startTime = this.startTime;
      // var startTime = "";
      var station = this.station;
      // var station = "";
      var pageNum = 1;

      var res = await AppBace.request(
        url,
        "POST",
        {
          endTime,
          pageSize,
          startTime,
          station,
          pageNum
        },
        this.token
      );
      console.log(res);
      this.list = res.data.data.records;
      this.total = res.data.data.total;
    },
    onLoad() {
      // setTimeout(() => {
      if (this.refreshing) {
        this.list = [];
        this.refreshing = false;
        this.pageSize = 10;
      } else {
        this.pageSize = this.pageSize + 5;
      }

      this.initDataRequest();
      this.loading = false;

      if (this.list.length === this.total) {
        this.finished = true;
      }
      // }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    }
  }
};
</script>
<style  scoped>
.imageSize img{
  width: 16px;
  height: 16px;
}
.flexBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
}
.rightButton {
  margin-right: 20px;
  /* background-color: white; */
  /* padding: 3px 10px; */
}
.boxBottom {
  padding-left: 20px;
  margin-top: 56px;
}
.condition {
  font-size: 12px;
}
.timeChoice {
  height: 40px;
  display: flex;
  align-items: center;
}
.stationStyle {
  font-size: 16px;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.timeSelect {
  font-size: 14px;
  height: 40px;
  display: flex;
  align-items: center;
}
.headerFixed {
  padding-bottom: 10px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
}
.body {
  margin-top: 167px;
  padding: 0 10px;
}
.bodyContent {
  background-color: white;
  margin-top: 10px;
  padding: 0 10px;
  /* border-radius: 5px; */
  /* padding: 0 10px; */
}
.bodyTime {
  border-bottom: 1px solid rgb(238, 238, 238);
  color: grey;
  /* font-size: 12px; */
  height: 40px;
  display: flex;
  align-items: center;
}
.boxContent {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.heightCont {
  height: 30px;
  display: flex;
  align-items: center;
}
.resultCont {
  display: flex;
  align-items: center;
}
.resultstyle {
  border: 1px solid rgb(118, 248, 118);
  border-radius: 2px;
  color: rgb(118, 248, 118);
  font-size: 12px;
  padding: 3px 5px;
}
.noresultstyle {
  border: 1px solid red;
  border-radius: 2px;
  color: red;
  font-size: 12px;
  padding: 3px 5px;
}
</style>


